.toolbar{
    position: fixed;
    top:0;
    left:0;
    background: #5e03ab;
    height: 100%;
    width: 126px;
    border-radius: 0px 0px 70px 0px;
   
}

@media (max-width: 499px) {
    .toolbar {
        top: 875px;
        height:126px;
        width: 100%;
        border-radius: 0px 00px 0px 0px;
        padding-bottom: 40px;
    }
    
}
.toolbar_navigation{
    padding-top: 40px;

